<template>
  <div class="login_box">
    <div class="input_login">
      <input type="search" placeholder="手机号/邮箱" v-model="uname" autocapitalize="off">
      <input type="password" placeholder="密码" v-model="pwd" maxlength="18" @keydown.enter="cheack_n_p">
      <button @click="cheack_n_p">登录</button>
    </div>
    <div class="h1">
      <h1>账号：18212345678/zyh123@163.com</h1>
      <h1>密码：123456</h1>
    </div>
  </div>
</template>

<script>
import { mapGetters } from 'vuex'

export default {
  name: 'login',
  data () {
    return {
      uname: '',
      pwd: ''
    }
  },
  mounted () {
    if (this.getLogin) {
      this.$router.replace('/Welcome')
    }
  },
  computed: {
    ...mapGetters([
      'getLogin',
      'getuname',
      'getpwd'
    ])
  },
  methods: {
    cheack_n_p () {
      if (!/^1[34578][0-9]{9}/.test(this.uname) && !/[a-zA-Z0-9]{1,10}@[a-zA-Z0-9]{1,5}\.[a-zA-Z0-9]{1,5}/.test(this.uname)) {
        alert('请输入正确的手机号或邮箱')
      } else if (this.getuname.indexOf(this.uname) === -1 || this.pwd !== this.getpwd) {
        alert('用户名或密码错误')
      } else {
        this.$store.dispatch('setLogin', true)
        this.$router.replace('/Welcome')
      }
    }
  }
}
</script>

<style lang="less" scoped>
.input_login{
  margin-top: 1.35rem;
  input{
    height: 1rem;
    width: 100%;
    padding:.6rem;
    box-sizing:border-box;
    border:none;
    outline:none;
    font-size:.45rem;
    display:block;
  }
  button{
    margin: .6rem auto;
    display:block;
    width: 9rem;
    height: 1rem;
    font-size:.5rem;
    background:#4cd964;
    border:none;
    outline:none;
    color:#fff;
    border-radius:.1rem;
  }
}
.h1 {
  position: absolute;
  left: .5rem;
  margin-top: 1.5rem;
  h1{
    font-size: .5rem;
    text-align: left;
  }
}

</style>



